.articles__title{
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 87px;
  text-align: center;
}

.articles__slider{
  margin-bottom: 55px;
}

.article{
  background: $white;
}

.article-img{
  width: 100%;
}

.article-content{
  padding: 25px 25px 30px;
}

.article-data{
  color: $gray;
  font-size: 15px;
  text-align:right;
  margin: 0;
}

.article-title{
  font-size: 25px;
  max-width: 255px;
  margin-top: 15px;
  margin-bottom: 14px;
}

.article-text{
  margin-bottom: 35px;
}

.readMore{
  display: flex;
  align-items:center;
}

.readMore-img{
  margin-right: 6px;
}

.readMore-text{
  font-weight: 500;
  border-bottom: 1px solid transparent;
  transition: all .5s;

  &:hover{
    border-bottom: 1px solid #333333;
  }
}

.navigation{
  display: flex;
  margin-top: 59px;
}

.articles .navigation .swiper-button-prev,
.articles .navigation .swiper-button-next{
  position: relative;
  margin-right: 50px;
  color: $orange;

  &::after{
    font-size: 30px;
    line-height: 30px;
  }
}

.pages{
  margin-top: -21px;
  margin-left: 26px;
  position: relative;
  width: 33px;

  &::before{
    content:"";
    position: absolute;
    width: 1px;
    height: 100%;
    background-color: #333333;
    transform: rotate(35deg);
    right: 50%;
    top:0;
  }
}

.number-start{
  font-size: 19px;
  font-weight: 500;
}
.number-end{
  text-align: right;
  font-size: 12px;
  color: $gray;
}

@media(max-width: 480px){
  .articles__title{
    font-size: 39px;
    line-height: 38px;
    margin: 0 20px 50px;
  }

  .navigation{
    margin-top: 30px;
  }
}