.services{
  padding-bottom: 125px;
}

.services__inner{
  text-align: center;
  
}

.services__title{
  font-weight: 700;
  font-size: 48px;
  line-height: 54px;
  margin-bottom: 80px;
}

.services__items{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 14px;
}

.services__item{
  background: $white;
  width: 193px;
  min-height: 215px;
  padding: 30px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing:border-box;
  position: relative;

  &-text{
    font-size: 18px;
    font-weight: 500;
  }

  &.swiper-slide-thumb-active::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 14px;
    background: $white;
    bottom: -14px;
  }

  &:hover{
    cursor: pointer;
  }
}

.swiper-button-white{
  position: absolute;
  color: $orange;
}

.swiper-button-next{
  right:-20px;
}

.services__slider{
  display: flex;
  background: $white;
  padding: 43px 35px;
  box-sizing:border-box;
}

.services__slider-img{
  width: 50%;
}

.services__slider-content{
  width: 50%;
  text-align: left;
  margin-left: 60px;

  &_title{
    font-weight: 500;
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 18px;
    margin-top: 24px;
  }

  &_text{
    color: $gray;
    margin-bottom: 9px;
  }
}

.services__slider-content-btn{
  margin-top: 34px;
  display: flex;
}

.btn-price{
  display: flex;
  align-items: center;
  margin-right: 21px;
}

.btn-price-img{
  margin-right: 6px;
}

.btn-doc{
  display: flex;
  align-items: center;

  &-img{
     margin-right: 6px;
  }
}

@media(max-width: 1240px){

  .services__slider{
  padding: 20px 15px;
  }

  .services__slider--img{
  width: 90%;
  }

  .services__slider-content{
  margin-left: 0px;
  }

  .services__slider-content_title{
    font-size: 24px;
    margin: 18px 0 12px;
  }

  .services__slider-content_text{
    margin: 8px 0 5px;
  }

  .services__slider-content-btn{
  margin-top: 24px;
  }
}

@media(max-width: 1050px){

  .services__slider--img{
  width: 80%;
  }

  .services__slider-content_title{
    font-size: 20px;
    margin: 0px 0 8px;
  }

  .services__slider-content_text{
    font-size: 14px;
    margin: 6px 0 5px;
  }

  .services__slider-content-btn{
  margin-top: 14px;
  }

  .btn-price,
  .btn-doc{
    padding: 0 20px;
  }
}

@media(max-width: 830px){


  .services{
  padding-bottom: 60px;
  }

  .services__slider{
    justify-content: center;
  }

  .services__slider-img{
    display: none;
  }

  .services__slider-content{
  width: 100%;
  max-width: 500px;
  }

  .services__slider-content_title{
    text-align: center;
  }

  .services__slider-content_text{
    text-align: center;
  }

  .services__slider-content-btn{
    justify-content:center;
  }
}

@media(max-width: 480px){

  .services__slider-content-btn{
    flex-direction: column;
    align-items:center;
  }

  .btn-price{
    width: 206px;
    margin: 0 0;
  }

  .btn-doc{
    margin-top: 6px;
  }
}

@media(max-width: 420px){

  .services__title{
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 50px;
  }
}
