html{
  box-sizing: border-box;
}

*,
*::after,
*::before{
  box-sizing:inherit;
}

body{
  color: #333333;
  font-family: 'Futura PT', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  background: #E5E5E5;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a{
  text-decoration: none;
  color: #333333;
} 

.container{
  width: calc(100% - 100px);
  max-width: $container;
  margin: 0 auto;
}

.btn{
  color: $white;
  line-height: 50px;
  font-weight: 500;
  padding: 0 47px;
  transition: all .5s;
  border: none;
  border-radius: 3px;
  display: inline-block;
}

.btn-black{
  background: #333333;

  &:hover{
    background: lighten($color: #333, $amount: 10%)
  }
}

.btn-orange{
  background: $orange;

  &:hover{
    background: lighten($color: #FFBD54, $amount: 10%)
  }
}

@media(max-width: 1180px){
   .container{
    width: calc(100% - 50px);
  }
}

@media(max-width: 810px){
  .container{
    width: calc(100% - 40px);
  }
}

@media(max-width: 600px){
  .container{
    width: calc(100% - 30px);
  }
}

@media(max-width: 480px){
  .container{
    width: calc(100% - 20px);
  }
}

@media(max-width: 420px){
  .container{
    width: calc(100% - 10px);
  }
}

