.footer__content{
  display: flex;
  justify-content: space-between;
  margin-bottom: 28px;
}

.menu__list3 .menu__list-link{
  margin-bottom: 14px;
  font-weight: 500;
}

.footer__mode{
  font-weight: 500;
}

.footer__mode-str{
  display: flex;

  &.str2{
    margin-left: 25px;
  }
}

.footer__mode-img{
  margin-right: 9px;
}

.footer__addres{
  display: flex;
  align-items: baseline;
}
.footer__addres-img{
  margin-right: 9px;
}

.footer__addres-text{
  width: 157px;
  font-weight: 500;
}

.footer__phone{
  display: flex; 
  flex-direction: column;
  font-weight: 500;
}

.footer__phone-img{
  margin-right: 9px;
}

.footer__phone-number-str{
  margin-left: 25px;
}

.footer__example{
  text-align: right;
}

@media(max-width: 1024px){
  .logo__link{
    width: 100px;
  }
}

@media(max-width: 992px){

  .footer__content{
    flex-wrap: wrap;
  }
}

@media(max-width: 700px){
  .footer__content{
    font-size: 14px;
  }
}

@media(max-width: 650px){
  
  .footer__content{
    font-size: 12px;
  }
  
  .logo__link{
    width: 70px;
  }

  .footer__addres{
    width: 150px;
  }
}

@media(max-width: 550px){

  .logo__link{
    width: 60px;
  }

  .footer__addres{
    width: 100px;
  }
}

@media(max-width: 480px){
  
  .footer__content{
    flex-direction: column;
    align-items:center;
    font-size: 14px;
    text-align: center;
  }


  .footer__mode{
    margin-top: 6px;
  }

  .footer__addres{
    width: auto;
    margin-top: 6px;

    &-text{
      width: auto;
    }
  }

  .footer__phone{
    margin-top: 6px;
  }

  .footer__example{
    text-align: center;
  }
}