.feedback{
  margin: 112px 0 247px;
  background: #333 url('../images/Group-feedback.png') no-repeat calc(50% + 510px) 0;
}

.feedback__inner{
  display: flex;
}

.feedback__map{
  width: 30%;
}

.feedback__form{
  width: 70%;
  padding-left: 165px;
}

.feedback__form-content{
  width: 375px;
}

.feedback__title{
  font-size: 48px;
  line-height: 48px;
  font-weight: 700;
  color: $white;
  margin: 60px 0 14px;
}

.feedback__text{
  color: $gray;
  width: 334px;
  margin-bottom: 29px;
}

.feedback__input{
  display: block;
  margin-bottom: 14px;
  padding: 16px 13px;
  width: 100%;
}

.feedback__textarea{
  width: 100%;
  height: 165px;
  margin-bottom: 30px;
  padding: 22px 17px;
}

.btn-feedback{
  border-radius: 0;
  margin-bottom: 74px;
  cursor: pointer;
}

@media(max-width: 1200px){

  .feedback{
  background-image: none;
  margin: 112px 0 150px;
  }
}

@media(max-width: 992px){

  .feedback{
  margin: 112px 0 50px;
  }


  .feedback__form{
    align-items:center;
    padding-left: 100px;
  }
}

@media(max-width: 768px){

  .feedback{
  margin: 60px 0 50px;
  }

  .feedback__inner{
    flex-direction: column;
  }

  .feedback__map{
  width: 100%;
  height: 300px;
}

.feedback__form{
  width: 100%;
  align-items:center;
  text-align: center;
  padding: 0;
  }

  .feedback__form-content{
    margin: 0 auto;
  }

  .feedback__text{
    text-align: center;
    width: auto;
  }
}

@media(max-width: 480px){

  .feedback__form-content{
    width: 300px;
  }

  .feedback__title{
    font-size: 47px;
    line-height: 47px;
    margin: 30px 0 14px;
  }

   feedback__text{
    width: 300px;
  }

  .btn-feedback{
    margin-bottom: 40px;
  }
}

@media(max-width: 420px){

  .feedback__form-content{
    width: 250px;
  }

  .feedback__title{
    font-size: 45px;
    line-height: 45px;
  }
}