.hero{
  padding: 150px 0 290px;
  background: url('../images/Group1.png') no-repeat calc(50% - 470px) -100px, url('../images/Group2.png') no-repeat calc(50% + 700px) 120%;
}

.hero__inner{
  display: flex;
}

.hero__content{
  max-width: 610px;
  margin-left: auto;
}

.hero__title{
  font-weight: 700;
  font-size: 70px;
  line-height: 64px;
  margin-bottom: 29px;

  &-blue{
    color: $blue;
  }
}

.hero__text{
  line-height: 21px;
  color: $gray;
  max-width: 495px;
  margin-bottom: 38px;
}

@media(max-width: 1250px) {
  .hero{
    background-position-y: -100px, -13000px;
  }
}

@media(max-width: 1180px) {

  .hero{
    background: none;
    padding: 0px 50px 100px;
    margin-left: 0;
  }

  .hero__content{
    max-width: 90%;
    text-align: center;
    margin-left: 49px;
  }

  .hero__text{
    max-width: 90%;
  }
}

@media(max-width: 950px) {
.hero__content{
    margin-left: 42px;
  }
}

@media(max-width: 810px) {

   .hero{
     padding: 0px 30px 100px;
   }

  .hero__title{
    font-size: 60px;
    line-height: 60px;
  }

  .hero__content{
    margin-left: 32px;
  }
}

@media(max-width: 600px) {

  .hero{
     padding: 0px 20px 100px;
   }

  .hero__title{
    font-size: 45px;
    line-height: 45px;
  }
}

@media(max-width: 500px) {

  .hero{
     padding: 0px 20px 50px;
   }

  .hero__content{
    margin-left: 24px;
  }
}

@media(max-width: 420px) {

  .hero{
     padding: 0px 10px 50px;
   }

  .hero__title{
    font-size: 35px;
    line-height: 35px;
  }
}

