.subs{
  margin-bottom: 150px;
  background: $blue url('../images/Group-subs.png') no-repeat 0 0;
  background-size: cover;
}

.subs__inner{
  position: relative;
}

.subs__form{
  display: flex;
  flex-direction: column;
  align-items:flex-start;
}

.subs__title{
  font-size: 48px;
  line-height: 60px;
  font-weight: 700;
  color: $white;
  margin-left: 287px;
  margin-top: 90px;
}

.subs__mail{
  display: flex;
  margin-left: 287px;
  margin-bottom: 88px; 
}

.subs__input{
  border: none;
  border-radius: 3px;
  width: 335px;
  padding: 0 22px;

  &::placeholder{
    color: $gray;
  }
}

.btn-subs{
  margin-left: 21px;
  cursor: pointer;
}

.subs-img{
  position: absolute;
  right: -20px;
  bottom: -54px;
}

@media(max-width: 1200px){

  .subs__form{
    align-items:center;
  }

  .subs-img{
  display: none;
}

.subs__title{
   margin-left: 0;
  }

.subs__mail{
   margin-left: 0;  
  }
}

@media(max-width: 1024px){
  .subs{
  margin-bottom: 60px;
  }
}


@media(max-width: 600px){

  .subs__title{
    font-size: 40px;
    line-height: 40px;
    max-width: 500px;
    margin-top: 50px;
  }

  .subs__mail{
    flex-direction: column;
  }

  .subs__input{
    height: 50px;
  }

  .btn-subs{
    margin-top: 10px;
    margin-left: 0;
  }
}

@media(max-width: 450px){

  .subs__form{
    padding: 30px 30px;
  }

  .subs__title{
    text-align: center;
    font-size: 35px;
    margin-top: 30px;
    margin-bottom: 20px;
  }

  .subs__mail{
    margin-bottom: 55px;
  }

  .subs__input{
    width: 220px;
  }
}