.header{
  background:  #F9F8F8;
  font-family: 'Futura PT';
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #333333;
}

.header__in{
  display: flex;
  flex-direction: column;
}

.header__info{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  margin-left: 190px;
  font-family: 'Futura PT';
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: #333333;
}

.header__info-addres{
  display: flex;
}

.header__info-img{
  margin: 0 9px;
}

.header__info-phone{
  display: flex;
}

.header__info--phone{
  display: flex;
  justify-content: space-around;
  text-align: center;
}

.header__info---phone{
  font-family: 'Futura PT';
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: #333333;
}

.header__info-mode{
  display: flex;
}

.header__info-text + .header__info-text{
  margin-left: 15px;
}

.header__inner{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
}

.logo{
  position: relative;
  z-index: 2;
}

.menu__list{
  display: flex;
}

li{
  list-style: none;
}

.menu__item{
  display: flex;
  margin: 0 25px;
}

.menu__item-img{
  margin-right: 9px;
}

.menu__list-link{
  font-weight: 500;
  line-height: 21px;
  border-bottom: 1px solid transparent;
  transition: all .5s;

  &:hover{
    border-bottom: 1px solid #333333;
  }
}

.menu__btn{
  display: none;
}

.question{
  display: flex;
}

.question-img{
  margin-right: 9px;
}

.question-text{
  color: #333333;
  font-weight: 500;
  line-height: 21px;
  border-bottom: 1px solid transparent;
  transition: all .5s;

  &:hover{
    border-bottom: 1px solid #333333;
  }
}

@media(max-width: 1250px) {

  .header__info{
    height: auto;
    margin-left: 0;
  }
  .header__info-phone{
    flex-direction: column;
    margin-top: 10px;
  }
}

@media(max-width: 1180px){
  
  .menu__item{
    margin: 0 9px;
  }
}

@media(max-width: 950px){

  .menu{
    order: 3;
  }

  .menu__list{
    display: flex;
    justify-content: space-evenly;
    position: absolute;
    top: 133px;
    right: 0;
    width: 100%;
    background-color: #F9F8F8;
    transform: scaleY(0);
    transform-origin:top;
    transition: all .5s;

    &.menu__list--active{
    transform: scaleY(1);
  }
  }

  .menu__list-link{
    padding: 9px 0;
  }

  .menu__btn{
    display: block;
    cursor: pointer;
    width: 35px;
    height: 24px;
    border:none;
    border-top: 2px solid #333;
    border-bottom: 2px solid #333;
    background-color: transparent;
    position: relative;

    &::before{
      content: '';
      position: absolute;
      width: 35px;
      height: 2px;
      background-color: #333;
      left: 0;
      top: 9px;
    }
  }
}

@media(max-width: 810px){
  
  .header__info{
    align-items:flex-start;
    order: 1;
  }

  .header__info-addres{
    max-width: 200px;
  }

  .header__info-phone{
    margin-top: 0;
  }
  
  .header__info-mode{
    display: flex;
    flex-direction: column;
    align-items:flex-end;
  }

  .img-mode{
    display: none;
  }

  .header__info-text{
  margin: 4px 0;
}

.menu__list{
  top: 124px;
}

  .logo__link{
    width: 120px;
  }
}

@media(max-width: 600px){
  
  .header__info{
    flex-wrap: wrap;
    font-size: 12px;
  }

  .header__info-addres{
    max-width: 150px;
  }

  .header__info---phone{
    font-size: 12px;
  }

  .logo__link{
    width: 100px;
  }

  .menu__list{
    flex-direction: column;
    top: 120px;
    right: 0; 
    align-items:center;
  }

  .question-img{
  margin-right: 4px;
  }

  .question-text{
    font-size: 12px;
  }

  .menu__btn{
    transform: scale(0.7);
  }
}

@media(max-width: 480px){

  .header__info-addres{
    max-width: 110px;
  }

  .header__inner{
    height: 50px;
  }

  .logo__link{
    width: 80px;
  }

  .menu__list{
    top: 110px;
  }

  .question-img{
    transform: scale(0.8);
  }
}

@media(max-width: 420px){

  .header__info{
    flex-direction: column;
    align-items:center;
  }

  .header__info-addres{
    max-width: 100%;
  }

  .header__info-text{
    margin: 0 0;
  }

  .header__info-text + .header__info-text{
    margin-left: 0;
  }

  .menu__list{
    top: 155px;
  }
}
