.reviews{
  padding-bottom: 120px;
  display: flex;
}

.reviews__content{
  text-align: center;
  max-width: 753px;
  margin: 0 auto;
  position: relative;

  &-img{
    color: #DBDBDB;
    opacity: 0.1;
    padding: 0 40px;
    position: absolute;

    &.img-left{
      left: -100px;
      top: -20px;
    }

    &.img-right{
      right: -100px;
      bottom: -20px;
    }
  }

  &::before,
  &::after {
    position: absolute;
    content: "";
    width: 140px;
    height: 1px;
    background-color: $gray;
    left: 50%;
    transform: translateX(-50%);
  }

  &::before{ 
    top: -40px;
  }

  &::after{
    top: 115px;
  }
}

.reviews__text{
  font-size: 20px;
  line-height: 26px;
  color: $gray;
}

.reviews__name{
  padding-top: 70px;
  text-align: center;
}

.reviews__name-clinic{
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
}

@media(max-width: 1200px){

.reviews{
  padding-bottom: 50px;
  }
}

@media(max-width: 950px){

  .reviews__content{
    max-width: 600px;

    &::after{
      top: 150px;
    }
  }

  .reviews__name{
  padding-top: 50px;
}
}

@media(max-width: 810px){

  .reviews{
  padding-bottom: 0px;
  }

  .reviews__content{
    max-width: 500px;
  }
}

@media(max-width: 700px){
  .reviews__content{
    max-width: 300px;

    &::after{
      top: 230px;
    }
  }
}

@media(max-width: 600px){

  .reviews__content::after{
    top: 190px;
  }

  .reviews__name-clinic{
    font-size: 16px;
  }

  .reviews__text{
  font-size: 16px;
}

.reviews__name{
  padding-top: 30px;
}
}

@media(max-width: 500px){
  
  .reviews__content{
    max-width: 200px;
  }

  .reviews__content::after{
    top: 270px;
  }
}

@media(max-width: 420px){

  .reviews__content{
    max-width: 150px;
  }

  .reviews__content::after{
    top: 300px;
  }

  .reviews__name-clinic{
    font-size: 14px;
  }

  .reviews__text{
  font-size: 14px;
}
}

@media(max-width: 350px){

  .reviews__content{
    max-width: 120px;

    &::after{
      top: 390px;
    }
  }
}


